.pagination {
   margin-top: 100px;
   margin-bottom: 25px;
   text-align: center;
   @include respond-to($laptop) {
      margin: 75px 0 75px 0; }
   @include respond-to($tablet) {
      margin: 75px 0 75px 0; }
   @include respond-to($phablet) {
      margin: 75px 0 75px 0; }
   @include respond-to($phone) {
      margin: 75px 0 75px 0; } }

.pagination-single {
   display: flex;
   justify-content: space-between;
   max-width: 85%;
   margin: 100px auto 0 auto;
   font-weight: 600;
   @include respond-to($laptop) {
      margin-top: 75px;
      margin-bottom: 75px; }
   @include respond-to($tablet) {
      margin-top: 75px;
      margin-bottom: 75px; }
   @include respond-to($phablet) {
      margin-top: 75px;
      margin-bottom: 75px; }
   @include respond-to($phone) {
      margin-top: 75px;
      margin-bottom: 75px; } }

